body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card {
    border: 4px #aba1a5 solid;
    border-radius: 2px;
    box-shadow: 4px 4px 10px rgb(56, 56, 56);
    height: 150px;
    margin: 1rem;
    position: relative;
    transition: all .2s ease-in-out;
    width: 150px;
}

.card > .img-container {
    background: rgb(255, 255, 255);
    height: 100%;
    overflow: hidden;
    width: 100%;
}

.card > .img-container > img {
    height: 100%;
    width: 100%;
}

.card:active {
    -webkit-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
}

.card:hover {
    box-shadow: 8px 8px 20px #4c3c3c;
    cursor: pointer;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
nav {
    width: 100%;
    font-family: 'Berkshire Swash', cursive;
    font-size: 3rem;
    color: #aba1a5;
    margin-bottom: 50px;
    background-color:	#2f3c37;
    height: auto;
}

a {
    color: #aba1a5;
}

a:hover {
    color: #aba1a5;
    text-decoration: none;
}

nav li {
    display: inline-block;
    padding: 26px 0;
    width: 25%;
}

ul {
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
}

@media only screen and (max-width: 750px) {
    #current, #top {
        font-size: 2rem;
    }
}
.title {
    color: #ececec;
    font-family: 'Berkshire Swash', cursive;
    font-size: 2.5em;
    margin-bottom: 50px;
    padding-left: 3%;
    padding-right: 3%;
    text-align: center;
    width: 100%;
}
.wrapper {
    -webkit-align-content: flex-start;
            align-content: flex-start;
    background: rgb(175, 175, 175);
    background-image: url('https://imgix.bustle.com/uploads/image/2018/12/18/dc990bc3-5c87-46a7-858e-71e6a51cdf5a-iron-throne.jpg?w=1020&h=574&fit=crop&crop=faces&auto=format&q=70&dpr=2');
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    height: 100%;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    overflow: auto;
    padding-bottom: 50px;
    width: 100%;
}
.title {
  width: center;
  margin-bottom: 50px;
}
